import Box from '@mui/material/Box';

import {splitEventsInPastFuture} from '../../../../common/services/events-service';
import {events} from '../../events-data-base/events';
import {EventData} from '../../events-data-base/types';

import PageDescription from './page-description/PageDescription';
import EventList from './event-list/EventList';

export default function EventsPage() {
	const {past, future} = splitEventsInPastFuture<EventData>(events);
	return (
		<Box sx={{p: 2}}>
			<PageDescription />
			<EventList title="Evenimente viitoare" events={future} />
			<EventList title="Evenimente precedente" events={past} />
		</Box>
	);
}
