import moment from 'moment';

function sortEvents<T extends {date: Date}>(events: T[]) {
	return events.sort((event1, event2) => {
		const time1 = moment(event1.date);
		const time2 = moment(event2.date);
		if (time1.isSame(time2)) {
			return 0;
		}

		if (time1.isBefore(time2)) {
			return -1;
		}
		return 1;
	});
}

function splitEventsInPastFuture<T extends {date: Date}>(events: T[]) {
	return events.reduce(
		(accumulator: {past: T[]; future: T[]}, event: T) => {
			const today = moment();
			if (moment(event.date).isBefore(today)) {
				return {
					...accumulator,
					past: [...accumulator.past, event]
				};
			} else {
				return {
					...accumulator,
					future: [...accumulator.future, event]
				};
			}
		},
		{past: [], future: []}
	);
}

export {splitEventsInPastFuture, sortEvents};
