import {Box, Typography} from '@mui/material';
import {Link} from 'react-router-dom';

import {sortEvents, splitEventsInPastFuture} from '../../../../../common/services/events-service';
import {events} from '../../../events-data-base/events';
import {EventData} from '../../../events-data-base/types';
import EventList from '../../events-page/event-list/EventList';

export default function UpcomingEvents() {
	const {future} = splitEventsInPastFuture(events);
	const sortedEvents = sortEvents<EventData>(future);
	return (
		<>
			<Box maxWidth="md" mx="auto" textAlign="center" marginTop={2}>
				<Typography variant="h6" display="inline">
					Mai multe informatii despre evenimentele noastre găsiți{' '}
				</Typography>
				<Typography variant="h6" display="inline" color="textWarning">
					<Link to="/events-page">aici</Link>
				</Typography>
			</Box>
			<EventList events={sortedEvents.slice(0, 2)} title={''} />
		</>
	);
}
